<template>
	<div>
		<el-row>
			<el-tooltip effect="light" placement="left-start"
					v-for="(item,index) in teamsView.slice((pageNow-1)*pageSize,pageNow*pageSize)"
					:key="index">
				<div slot="content" style="font-size: 14px;margin-bottom: 6px;">{{item.team}}</div>
				<!-- <el-button>dvs</el-button> -->
				<div slot="content" style="font-size: 13px;margin-bottom: 6px">
					<span>{{item.names}}</span> 
					<span>{{item.masterwork}}</span> 
					<span>{{item.work}}</span>
					<span>{{item.type}}</span>
					<!-- <span>{{item.count}}</span> -->
				</div>
				<p slot="content" style="width: 300px" class="discription">{{item.discription}}</p>
				<el-card shadow="hover" 
				style="width: 150px;height: 300px;float: left;margin:0px 10px;" 
				bodyStyle="padding:10px" >
					<div class="cover">
						<img class="img" :src="item.cover" alt="海报">
						<!-- {{item.cover}} -->
					</div>
					<br />
					<div class="title">
						<span>{{item.team}}</span> <br />
						<span>得票：{{item.count}}</span><br /><br />
						<el-button size="small" @click="vote(item)">为他投票</el-button>
					</div>
					
					<!-- <p class="author">{{item.work}}</p> -->
				</el-card>
				
			</el-tooltip>
		</el-row>
		<el-row >
			<el-col :span="8" :offset="13">
				<el-pagination style="margin: 20px 0px;"
					background
					prev-text="上一页"
					next-text="下一页"
					:page-size="pageSize"
					:current-page="pageNow"
					:page-sizes=[2,4,6,8,10]
					layout="prev, pager, next,jumper,total,sizes"
					:total="total"
					@current-change="findPage"
					@size-change="findSize">
				</el-pagination>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	// import Guo from '../../assets/teamsImg/郭阳郭亮.jpg'
	// import Lu from '../../assets/teamsImg/卢鑫玉浩.jpg'
	// import ZhangHuo from '../../assets/teamsImg/张聿霍星辰.png'
	// import Ye from '../../assets/teamsImg/叶逢春.jpg'
	// import ZhangSJ from '../../assets/teamsImg/张霜剑.jpg'
	
	export default{
		data(){
			return{
				teamsView:[
					{
						cover:'',
						team:'',
						names:'',
						masterwork:'',
						work:'',
						type:'',
						discription:'',
						count:0
					}
				],
				// imgs:[Guo,Lu,ZhangHuo,Ye,ZhangSJ],
				total:0,
				pageNow:1,
				pageSize:4,
			}
		},
		props:["tag"],
		methods:{
			vote(item){
				console.log(item)
				this.$http.post("http://localhost:8081/proposal/vote",item).then(res=>{
					if(res.data.success){
						this.findAll(this.tag)
					}
				})
			},
			// 处理每页条数变化的相关方法
			findSize(size){
				console.log(size);
				this.pageSize=size;
				// this.findAll(this.pageNow,size);
			},
			// 处理与分页相关的方法
			findPage(page){
				console.log(page);
				this.pageNow=page;
				// this.findAll(page,this.pageSize);
			},
			findAll(kinds){
				console.log("kinds:"+kinds)
				this.$http.get("http://localhost:8081/proposal/findAll?kind="+kinds).then(res=>{
					// console.log(234)
					// console.log(res)
					if(res.data.success){
						// console.log(123)
						this.teamsView=res.data.result;
						// console.log(res.data.result)
					}
				})
			}
		},
		created() {
			this.findAll(this.tag);
		}
	}
</script>

<style>
	.cover {
		width: 115px;
		height: 172px;
		margin: auto;
		overflow: hidden;
		cursor: pointer;
	}
	.img {
		width: 115px;
		height: 172px;
	
	}
	.author {
		color: #333;
		width: 102px;
		font-size: 13px;
		margin-bottom: 6px;
		text-align: left;
	}
	.title {
		font-size: 14px;
		text-align: center;
	}
	.discription {
		display: block;
		line-height: 17px;
	}
</style>
