<template>
	<div>
		<el-row :gutter="20">
			
			<el-col :span="1" :offset="1" >
				<el-button type="primary" size="small" icon="el-icon-edit" :disabled="ok" @click="editInfo"></el-button>
			</el-col>
			<el-col :offset="1" :span="1">
				<el-button type="primary" size="small" icon="el-icon-delete" :disabled="ok" @click="deleteInfo"></el-button>
			</el-col>
			<el-col :offset="3" :span="13">
				<el-input
					@keyup.enter.native="searchClick"
					placeholder="通过队名或队员姓名搜索..."
					prefix-icon="el-icon-search"
					size="small"
					style="width: 280px;"
					v-model="keywords">
				</el-input>
				<el-button size="small" type="primary" icon="el-icon-search" @click="searchClick">搜索</el-button>
			</el-col>
			<el-col :offset="2" :span="1">
				<el-button size="small" type="primary" @click="dialogVisible = true; title='报名参赛'">添加</el-button>
			</el-col>
		</el-row>
		<el-dialog
			:title="title"
			:visible.sync="dialogVisible"
			width="50%"
			:before-close="handleClose">
			<el-form label-position="left" :rules="rules" label-width="auto" :model="formLabelAlign" label-suffix=":">
				<!-- <el-row>
					<el-col :span="16" :offset='3'>
						<el-form-item label="参赛队伍" prop="team">
							<el-input v-model="formLabelAlign.team" placeholder="请输入队伍名称"></el-input>
						</el-form-item>
					</el-col>
				</el-row> -->
				<el-row>
					<el-col :span="16" :offset='3'>
						<el-form-item label="参赛队伍" prop="team">
							<el-input v-model="formLabelAlign.team" placeholder="请输入队伍名称"></el-input>
						</el-form-item>
					</el-col>
				</el-row>
				<el-row>
					<el-col :span="16" :offset='3'>
						<el-form-item label="参赛队员" prop="names">
							<el-input v-model="formLabelAlign.names" placeholder="请输入队员姓名"></el-input>
						</el-form-item>
					</el-col>
				</el-row>
				<el-row>
					<el-col :span="16" :offset='3'>
						<el-form-item label="代表作品" prop="masterwork">
							<el-input v-model="formLabelAlign.masterwork" placeholder="请输入代表作品"></el-input>
						</el-form-item>
					</el-col>
				</el-row>
				
				<el-row>
					<el-col :span="16" :offset='3'>
						<el-form-item label="参赛作品" prop="work">
							<el-input v-model="formLabelAlign.work" placeholder="请输入作品名称"></el-input>
						</el-form-item>
					</el-col>
				</el-row>
				<el-row>
					<el-col :span="16" :offset='3'>
						<el-form-item label="海报" prop="cover">
							<el-input v-model="formLabelAlign.cover" placeholder="请输入图片URL"></el-input>
							<img-upload @onUpload="imgUpload" ref="imgUpload"></img-upload>
						</el-form-item>
					</el-col>
				</el-row>
				<el-row>
					<el-col :span="16" :offset='3'>
						<el-form-item label="作品类型" prop="type">
							<el-select v-model="formLabelAlign.type" placeholder="请选择作品类型">
								<el-option
									v-for="item in options"
									:key="item.value"
									:label="item.label"
									:value="item.label">
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
				</el-row>
				<el-row>
					<el-col :span="16" :offset='3'>
						<el-form-item label="队伍简介" prop="discription">
							<el-input v-model="formLabelAlign.discription" type='textarea' placeholder="介绍一下你的队伍吧~"></el-input>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="AddInf">确 定</el-button>
			</span>
		</el-dialog>
		<!-- 用一个表格展示所有的参赛队伍信息 -->			
		<el-table :height="230"
			:data="tableData"
			stripe
			@selection-change="handleSelectionChange"
			style="width: 100%">
			<el-table-column
				type="selection"
				width="55">
			</el-table-column>
			<el-table-column
				prop="id"
				label="编号"
				width="80">
			</el-table-column>
			<el-table-column
				prop="cover"
				label="海报"
				width="80">
			</el-table-column>
			<el-table-column
				prop="team"
				label="队伍名称"
				width="180">
			</el-table-column>
			<el-table-column
				prop="names"
				label="队员姓名"
				width="180">
			</el-table-column>
			<el-table-column
				prop="masterwork"
				label="代表作品">
			</el-table-column>
			<el-table-column
				prop="work"
				label="参赛作品">
			</el-table-column>
			<el-table-column
				prop="type"
				label="作品类型">
			</el-table-column>
			<el-table-column
				prop="discription"
				label="队伍简介">
			</el-table-column>
		</el-table>	
		<el-row >
			<el-col :span="8" :offset="11">
				<el-pagination style="margin: 20px 0px;"
					background
					prev-text="上一页"
					next-text="下一页"
					:page-size="pageSize"
					:current-page="pageNow"
					:page-sizes=[2,4,6,8,10]
					layout="prev, pager, next,jumper,total,sizes"
					:total="total"
					@current-change="findPage"
					@size-change="findSize">
				</el-pagination>
			</el-col>
		</el-row>
		<!-- <paginate></paginate> -->
	</div>
</template>

<script>
	import imgUpload from './imgUpload.vue'
	export default{
		data(){
			return{
				formLabelAlign:{
					team:'',
					names:'',
					masterwork:'',
					work:'',
					type:'',
					cover:'',
					discription:''
				},
				title:'',
				ok:true,
				total:0,
				pageNow:1,
				pageSize:4,
				options: [{
					value: '选项1',
					label: '相声'
					}, {
					value: '选项2',
					label: '小品'
					}, {
					value: '选项3',
					label: '默剧'
					}, {
					value: '选项4',
					label: '其他'
					},
				],
				tableData:[{
					id: '',
					team:'',
					names:'',
					masterwork:'',
					work:'',
					type:'',
					cover:'',
					discription:''
				}],
				keywords:'',
				// presentTags:this.presentTag,
				dialogVisible: false,
				multipleSelection: [],
				rules: {
					team: [
						{ required: true, message: '请输入队伍名称', trigger: 'blur' },
						{ max: 15, message: '长度在 15 个字符以内', trigger: 'blur' }
					],
					names: [
						{ required: true, message: '请输入队员姓名', trigger: 'blur' },
						{ max: 15, message: '长度在 15 个字符以内', trigger: 'blur' }
					],
					work:[
						{ required: true, message: '请输入参赛作品', trigger: 'blur' },
						{ max: 15, message: '长度在 15 个字符以内', trigger: 'blur' }
					],
					type:[
						{ required: true, message: '请选择作品类型', trigger: 'blur' }
					]
				}
			}
		},
		props:['presentTag'],
		methods:{
			handleClose(done) {
					this.$confirm('确认关闭？').then(() => {
						done();
					}).catch(() => {});
				},
			// 处理每页条数变化的相关方法
			findSize(size){
				console.log(size);
				this.pageSize=size;
				// this.findAll(this.pageNow,size);
			},
			// 处理与分页相关的方法
			findPage(page){
				console.log(page);
				this.pageNow=page;
				// this.findAll(page,this.pageSize);
			},
			imgUpload(){
				this.formLabelAlign.cover=this.$refs.imgUpload.url
			},
			AddInf(){
				this.dialogVisible = false
				console.log("xasd");
				this.$http.post("http://localhost:8081/proposal/addOrUpdate",this.formLabelAlign).then(res=>{
					if(res.data.success){
						// alert(345);
						this.formLabelAlign={}
						this.findAll(this.presentTag);
					}
				});
			},
			searchClick(){
				this.$http.get("http://localhost:8081/proposal/find?condition="+this.keywords).then(res=>{
					if(res.data.success){
						this.tableData=res.data.result
					}
				})
			},
			editInfo(){
				this.dialogVisible = true
				this.title = "修改信息"
				var item;
				for(item in this.multipleSelection){
					this.formLabelAlign = this.multipleSelection[item];
				}
				
			},
			deleteInfo(){
				this.$confirm('此操作将永久删除被选中文件, 是否继续?', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
						}).then(() => {
							var i;
							for(i=0;i<this.multipleSelection.length;i++){
								this.$http.get("http://rap2.taobao.org:38080/app/mock/264241/team/delete?team="+this.multipleSelection[i].team).then(res=>{
									if(res.data.success){
										this.$message({
										type: 'success',
										message: '删除成功!',
										center:true
										});
										this.findAll();
									}else{
										this.$message({
										type: 'error',
										message: '删除失败!',
										center:true
										});
									}
								})
							}
							
						}).catch(() => {
							this.$message({
							type: 'info',
							message: '已取消删除'
							});          
						});
			},
			handleSelectionChange(val) {
				this.multipleSelection = val;
				console.log(this.multipleSelection)
				if(val.length != 0){
					
					this.ok = false;
					
				}else{
					this.ok = true;
				}
				console.log(this.ok);
				// console.log(val)
				
			},
			findAll(kinds){
				console.log("kinds:"+kinds)
				this.$http.get("http://localhost:8081/proposal/findAll?kind="+kinds).then(res=>{
					// console.log(234)
					// console.log(res)
					if(res.data.success){
						// console.log(123)
						this.tableData=res.data.result;
						// console.log(res.data.result)
					}
				})
			}
		},
		components:{imgUpload},
		created() {
			console.log(this.presentTag)
			this.findAll(this.presentTag);
			
		},
		
	}
</script>

<style>
</style>
