<template>
	<div>
		<!-- <p>hello,metamask!!</p>
		<p>Metamask:{{ web3.isInjected }}</p>
		<p>Network: {{ web3.networkId }}</p>
		<p>Account: {{ web3.coinbase }}</p>
		<p>Balance: {{ web3.balance }}</p> -->
		<!-- <h1 ></h1> -->
		<el-row>
			<el-col :span="2" :offset="20">
				<el-button plain type="primary" @click="deploy">Deploy</el-button>
			</el-col>
		</el-row>
		<el-form label-position="left" :rules="rule" label-width="auto" :model="publishData" label-suffix=":">
			<el-row>
				<el-col :span="16" :offset='3'>
					<el-form-item label="Account" prop="account">
						<el-input v-model="web3Coinbase" ></el-input>					
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="16" :offset='3'>
					<el-form-item label="Gas Limit" prop="limit">
						<el-input v-model="publishData.limit" placeholder="请输入上限值"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row :gutter="10">
				<el-col :span="10" :offset='3'>
					<el-form-item label="Value" prop="value">
						<el-input v-model="publishData.value" placeholder="请输入金额"></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="3">
					<el-select v-model="publishData.unit">
						<el-option
							v-for="item in options"
							:key="item.value"
							:label="item.label"
							:value="item.value">
						</el-option>
					</el-select>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="16" :offset='3'>
					<el-form-item label="ABI" prop="Abi">
						<el-input type="textarea" v-model="publishData.Abi" placeholder="请输入合约的ABI"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="16" :offset='3'>
					<el-form-item label="ByteCode" prop="bytecode">
						<el-input type="textarea" v-model="publishData.bytecode" placeholder="请输入合约的字节码,以'0x'开头"></el-input>
					</el-form-item>
				</el-col>
			</el-row> 
			<el-row>
				<el-col :span="16" :offset='3'>
					<el-form-item label="At Address" prop="address">
						<el-input v-model="publishData.address" placeholder="请输入加载合约的地址"></el-input>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				publishData:{
					// account: this.$store.state.web3.coinbase,
					Abi:'',
					limit:'3000000',
					value:'0',
					unit:'wei',
					address:'',
					bytecode:''
				},
				rule:{
					Abi: [
						{ required: true, message: '请输入合约的ABI', trigger: 'blur' }
					],
					bytecode: [
						{ required: true, message: '请输入合约的字节码', trigger: 'blur' }
					],
				},
				options:[
					{
						value:'选项1',
						label:'wei'
					},
					{
						value:'选项2',
						label:'gwei'
					},
					{
						value:'选项3',
						label:'finney'
					},
					{
						value:'选项4',
						label:'ether'
					}
				]
			}
		},
		
		computed:{
			web3Coinbase:{
				get() {
					return this.$store.state.web3.coinbase
				},
				set (value) {
					this.$store.commit('updateMessage', value)
				}
				
			},
			
		},
		methods:{
			deploy(){
				this.$http.get("http://rap2.taobao.org:38080/app/mock/264241/contract/deploy?account="+this.$store.state.web3.coinbase+'?data='+this.publishData).then(res=>{
					if(res.data.success){
						this.$message({
							type:'success',
							message:'部署成功！！'
						});
					}
				})
			}
		},
		created() {
			// alert(123);
			console.log(this.$store.state)
		},
		watch:{
			$route:{
				handler:function(val){
					console.log(val)
					if(val.path=="/deploy/metamask"){
						alert(456);
					
					}
				}
			}
		}
		
	}
</script>

<style>
</style>
