import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Button from '../components/Button.vue'
import Index from '../components/Index.vue'
import List from '../components/users/List.vue'
import Deploy from '../components/dapp/deploy.vue'
// import Ballot from '../components/dapp/ballot.vue'
import Metamask from '../components/dapp/hello_metamask.vue'
import Teams from '../components/dapp/teams.vue'
import Login from "../components/users/login.vue"
import Vote from "../components/dapp/voteForThem.vue"

Vue.use(VueRouter)

  const routes = [
	{
		path:'/',
		redirect:'/index'
	},
  {
    path: '/home',
    name: 'Home',
    component: Home,
	children:[
		{path:'button',component:Button},
		]
  },
  {
	path:'/vote',
	component:Vote
  },
  {
	path:'/index',
	component:Index,
	meta:{
		requireAuth:true
	}
  },
  {
	path:'/user',
	component:List
  },
  {
	path:"/login",
	component:Login
  },
  {
	path:'/deploy',
	component:Deploy,
	
	children:[
		{path:'teams',component:Teams,meta:{requireAuth:true}},
		{path:'metamask',component:Metamask,meta:{requireAuth:true}}
	],
	
  }
  
  
]
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location, onResolve, onReject) {
  if (onResolve || onReject) return originalPush.call(this, location, onResolve, onReject)
  return originalPush.call(this, location).catch(err => err)
}
const router = new VueRouter({
  mode:"history",
  routes
})

export default router
