<template>
	<div>
		<el-table :height="230"
			:data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
			style="width: 100%">
			<el-table-column
				label="Id"
				width="180">
				<template slot-scope="scope">
				<i class="el-icon-s-custom"></i>
				<span style="margin-left: 10px">{{ scope.row.id }}</span>
				</template>
			</el-table-column>
			<el-table-column
				label="Name"
				width="180" >
				<template slot-scope="scope">
				<el-popover trigger="hover" placement="top">
					<p>Name: {{ scope.row.name }}</p>
					<p>Address: {{ scope.row.address }}</p>
					<div slot="reference" class="name-wrapper">
					<el-tag size="medium">{{ scope.row.name }}</el-tag>
					</div>
				</el-popover>
				</template>
			</el-table-column>
			<el-table-column label="Birthday" prop="bir"></el-table-column>
			<el-table-column label="Address" prop="address"></el-table-column>
			
			<el-table-column
					align="right">
					<template slot="header" >
					<el-input
						v-model="search"
						size="mini"
						placeholder="输入姓名关键字搜索"/>
					</template>
					<template slot-scope="scope">
					<el-button
						size="mini"
						@click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button
						size="mini"
						type="danger"
						@click="handleDelete(scope.$index, scope.row)">删除</el-button>
					</template>
			</el-table-column>
			
		</el-table>
		
		<!-- 设置分页组件 -->
		<el-row >
			<el-col :span="2" :offset="0">
				<el-button size='medium' plain round type='primary' style="margin:20px 0px" @click="show2=!show2">Add</el-button>
			</el-col>
			<el-col :span="8" :offset="11">
				<el-pagination style="margin: 20px 0px;"
					background
					prev-text="上一页"
					next-text="下一页"
					:page-size="pageSize"
					:current-page="pageNow"
					:page-sizes=[2,4,6,8,10]
					layout="prev, pager, next,jumper,total,sizes"
					:total="total"
					@current-change="findPage"
					@size-change="findSize">
				</el-pagination>
			</el-col>
		</el-row>
		
		<transition name="el-zoom-in-center">
				<div v-show="show2" class="transition-box">
					<el-form ref="form" :model="user" label-width="80px" label-suffix=":" :rules="rules">
						<el-form-item label="姓名" prop="name">
							<el-col :span="11">
								<el-input v-model="user.name" placeholder="请输入姓名" autofocus clearable prefix-icon='el-icon-user'></el-input>
							</el-col>
						</el-form-item>
						
						<el-form-item label="生日" prop="bir">
							<el-col :span="11">
								<el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="选择日期" v-model="user.bir" style="width: 100%;"></el-date-picker>
							</el-col>
						</el-form-item>
						<el-form-item label="详细地址" prop="address">
							<el-col :span="11">
								<el-input type="textareas" v-model="user.address" placeholder="请输入地址" clearable prefix-icon='el-icon-house'></el-input>
							</el-col>
						</el-form-item>
						<el-form-item>
							<el-button type="primary" @click="onSubmit('form')">提交</el-button>&nbsp;&nbsp;&nbsp;
							<el-button type="danger" @click="clearAll">重置</el-button>&nbsp;&nbsp;&nbsp;
							<el-button type="info" @click="cancel">取消</el-button>
						</el-form-item>
					</el-form>
				</div>
		</transition>
		
		
	</div>
</template>

<script>
	export default{
		data() {
				return {
					tableData: [],
					show2:false,
					search:'',
					user:{},
					total:0,
					pageNow:1,
					pageSize:4,
					rules: {
						name: [
							{ required: true, message: '请输入姓名',trigger: 'blur' },
							{ min: 2, max: 5, message: '长度在 2 到 5 个字符', trigger: 'blur' }
						],
						bir:[
							{required:true,message:'请输入出生日期',trigger:'blur'}
						],
						address:[
							{ required: true, message: '请输入详细地址',trigger: 'blur' },
							{ min: 10, max: 120, message: '长度在 10 到 120 个字符', trigger: 'blur' }
						],
					}
				}
			},
			methods: {
				// 处理每页条数变化的相关方法
				findSize(size){
					console.log(size);
					this.pageSize=size;
					this.findAll(this.pageNow,size);
				},
				// 处理与分页相关的方法
				findPage(page){
					console.log(page);
					this.pageNow=page;
					this.findAll(page,this.pageSize);
				},
				handleEdit(index, row) {
					console.log(index, row);
					this.$http.get("http://localhost:8081/user/findOne?id="+row.id).then(res=>{
						if(res.data.success){
							console.log(res.data.result);
							this.user = res.data.result;
							if(!this.show2){
								this.show2=!this.show2;
							}
						}
					})
				},
				handleDelete(index, row) {
					this.$confirm('此信息记录将永久删除该文件, 是否继续?', '提示', {
								confirmButtonText: '确定',
								cancelButtonText: '取消',
								type: 'warning'
							}).then(() => {
								this.$http.get("http://localhost:8081/user/delete?id="+row.id).then(res=>{
									console.log(row.id);
									if(res.data.success){
									this.$message.success(res.data.msg);
									this.findAll();
									}else{
									this.$message.error(res.data.msg);
									console.log(res.data.msg);
									}
								});
							}).catch(() => {
								this.$message({
									type: 'info',
									message: '已取消删除'
								});
							});
				console.log(index, row);
				},
				onSubmit(formName) {
					console.log(this.user);
					this.$refs[formName].validate((valid) => {
							if (valid) {
								this.$http.post("http://localhost:8081/user/saveOrUpdate",this.user).then(res=>{
									console.log(res.data);
									if(res.data.success){
										this.$message.success(res.data.msg);
										this.user = {};
										this.show2 = false;
										this.findAll();
									}else{
									this.$message.error(res.data.msg);
									console.log(res.data.msg);
									}
								});
							} else {
								console.log('error submit!!');
								this.$message.error("请完善提交信息！！");
							}
						});
					
				},
				findAll(page,size){
					page = page ? page : this.pageNow;
					size = size ? size : this.pageSize;
					this.$http.get('http://localhost:8081/user/findByPage?pageNow='+page+'&pageSize='+size).then(res=>{
						this.tableData=res.data.result;
						this.total=res.data.total;
					});
				},
				clearAll(){
					this.user={};
				},
				cancel(){
					this.clearAll();
					this.show2 = false;
				}
				
			},
			created() {
				this.findAll();
			}
		}
		
</script>

<style>
	.transition-box {
		margin-bottom: 10px;
		width: 100%;
		height: 400px;
		border-radius: 4px;
		/* text-align: center; */
		padding: 40px 20px;
		box-sizing: border-box;
		margin-right: 20px;
	}
</style>
