import Web3 from 'web3'
import {address,ABI} from './constants/ballotContract.js'

let getContract = new Promise(function(resolve){
	let web3 = new Web3(window.web3.currentProvider)
	let ballotContractInstance =new web3.eth.Contract(ABI,address)
	// let ballotContractInstance = ballotContract.At(address)
	// console.log(web3.eth)
	resolve(ballotContractInstance)
})
export default getContract