<template>
	<div>
		<el-row>
			
			<el-col :span="2" >
				<el-tabs tab-position="left" @tab-click="setTag">
					<el-tab-pane label="全部" ></el-tab-pane>
					<el-tab-pane label="小品"></el-tab-pane>
					<el-tab-pane label="相声"></el-tab-pane>
					<el-tab-pane label="默剧"></el-tab-pane>
				</el-tabs>
			</el-col>
			<el-col :span="19" >
				<voteView :tag="tabTag" ref="vote"></voteView>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	import voteView from './voteView.vue'
	export default{
		data(){
			return{
				tabTag:"全部"
			}
		},
		components:{voteView},
		methods:{
			setTag(tab){
				this.tabTag=tab.label
				this.$refs.vote.findAll(this.tabTag)
			}
		}
	}
</script>

