import Vue from 'vue'
import Vuex from 'vuex'
import state from './state.js'
import getWeb3 from '../util/getWeb3.js'
import pollWeb3 from '../util/pollWeb3.js'
import getContract from '../util/getContract.js'


Vue.use(Vuex)

export const store = new Vuex.Store({
  state,
  mutations: {
	login(state,user){
		console.log("login Mutation being executed",user)
		state.user=user
		window.localStorage.setItem("user",JSON.stringify(user))
	},
	registerWeb3Instance (state, payload) {
		console.log('registerWeb3instance Mutation being executed', payload)
		let result = payload
		let web3Copy = state.web3
		// console.log(state.user)
		web3Copy.coinbase = result.coinbase
		// console.log(web3Copy.coinbase)
		web3Copy.networkId = result.networkId
		web3Copy.balance = parseInt(result.balance, 10)
		web3Copy.isInjected = result.injectedWeb3
		web3Copy.web3Instance = result.web3
		state.web3 = web3Copy
		pollWeb3()
		// pollWeb3()
		// console.log(result.injectedWeb3)
	},
	updateMessage(state, message){
		state.web3.coinbase = message
	},
	pollWeb3Instance (state, payload) {
		console.log('pollWeb3Instance mutation being executed', payload)
		state.web3.coinbase = payload.coinbase
		state.web3.balance = parseInt(payload.balance, 10)
	},
	registerContractInstance (state, payload) {
		console.log('Casino contract instance: ', payload)
		state.contractInstance = () => payload
	}
	

  },
  actions: {
	registerWeb3 ({commit}) {
				console.log('registerWeb3 Action being executed')
				getWeb3.then(result => {
				console.log('committing result to registerWeb3Instance mutation')
				commit('registerWeb3Instance', result)
			}).catch(e => {
				console.log('error in action registerWeb3', e)
			})
		},
	pollWeb3 ({commit}, payload) {
		console.log('pollWeb3 action being executed')
		commit('pollWeb3Instance', payload)
	},
	getContractInstance ({commit}) {
			getContract.then(result => {
			commit('registerContractInstance', result)
		}).catch(e => console.log(e))
	}
		
	
  },
  modules:{},
  strict:true
})

