<template>
	<div>
		<el-form class="login_container" label-position="left">
			<h2 class="title">用户登录</h2>
			<el-form-item label="以太坊地址">
				<el-input type="text" v-model="loginForm.address" placeholder="请输入有效的以太坊地址"></el-input>
			</el-form-item>
			<el-form-item label="助记词">
				<el-input type="text" v-model="loginForm.memoryWords" placeholder="请输入12个助记词"></el-input>
			</el-form-item>
			<el-form-item>
				<el-button style="width: 100%;margin: auto;" @click="login">登录</el-button>
			</el-form-item>
			<el-form-item>
				<p style="font-size:smaller;">其他登录方式  </p>
				<el-button style="width: 100%;" size="medium" icon="el-icon-user-solid"  @click="metamaskLogin">Metamask 登录</el-button>
			</el-form-item>
			
		</el-form>
	</div>
</template>

<script>

	export default{
		data(){
			return{
				loginForm:{
					address:'',
					memoryWords:''
				},
				address:''
			}
		},
		methods:{
			login(){
				
			},
			metamaskLogin(){
				this.address=this.$store.state.web3.coinbase
					// console.log(window.localStorage.getItem("publicAddress"))
				// console.log(this.address)
				this.$http.get("http://localhost:8081/user/login?publicAddress="+this.address).then(res=>{
					if(res.data.success){
						// console.log(res.data.result.publicAddress)
						this.handleSignMessage(res.data.result.publicAddress,res.data.result.nonce);
						// this.$router.push('/deploy/metamask');
					}
				})
			},
			handleSignMessage(address,nonce){
				// console.log(window.web3);
				window.web3.personal.sign(
					window.web3.fromUtf8(`I am signing my one-time nonce: ${nonce}`), address, (err, signature) => {
						// console.log(768);
						if (err){
							console.log("error occured during signing: "+err)
							this.$message({
								type:'error',
								message:"请在Metamask上对nonce签名"
							})
						} else{
							this.handleAuthenticate(address, signature,nonce);
							// console.log(signature)
						}
						
					}
				)
			},
			handleAuthenticate(address, signature,nonce){
				
				var ethUtil = require('ethereumjs-util');
				// var _this = this;
				var msg = `I am signing my one-time nonce: ${nonce}`;
				msg = window.web3.toHex(msg)
				
				const msgBuffer = ethUtil.toBuffer(msg)
				const msgHash = ethUtil.hashPersonalMessage(msgBuffer);
				console.log(msgHash)
				const signatureBuffer = ethUtil.toBuffer(signature);
				const signatureParams = ethUtil.fromRpcSig(signatureBuffer);
				console.log(signatureParams)
				const publicKey = ethUtil.ecrecover(msgHash, signatureParams.v, signatureParams.r, signatureParams.s);
				const addressBuffer = ethUtil.publicToAddress(publicKey);
				const transaddress = ethUtil.bufferToHex(addressBuffer);
				console.log(transaddress)
				if(transaddress.toLowerCase() === address.toLowerCase()){
					console.log(123)
					this.$message({
						type:'success',
						message:'登陆成功'
					})
					console.log("logging successfully,and login Mutation being commited");
					this.$store.commit('login',{userAddress: this.address})
					
					var path = this.$route.query.redirect
					// console.log(path)
					this.$router.push({path: (path == '/' || path == undefined) ? '/index' : path})
					// console.log(this.$route.path)
				}
				
			},
			
		},
	}
</script>

<style>
	.title{
		text-align: center;
		color: #000000;
		margin: 0px auto 40px auto;
	}
	.login_container{
		border-radius: 15px;
		background-clip: padding-box;
		margin: 90px auto;
		width:350px;
		padding: 35px 35px 15px 35px;
		background:#fff;
		border: 1px solid #eaeaea;
		box-shadow: 0px 0px 25px #cac6c6;
	}
</style>
